import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { 
  CreditCard, 
  Plus, 
  Edit3, 
  Trash2, 
  Calendar,
  DollarSign,
  AlertCircle,
  CheckCircle,
  ArrowDown,
  ArrowUp
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

interface CreditCard {
  id: string;
  name: string;
  bank: string;
  creditLimit: number;
  usedAmount: number;
  availableAmount: number;
  startDate: string;
  endDate: string;
  reminderDate: string;
  color: string;
  holderName?: string;
  withdrawLimit?: number;
  depositLimit?: number;
  withdrawUsed?: number;
  depositUsed?: number;
}

interface CreditCardsDialogProps {
  isOpen: boolean;
  onClose: () => void;
}

const CreditCardsDialog: React.FC<CreditCardsDialogProps> = ({ isOpen, onClose }) => {
  console.log('CreditCardsDialog rendered with isOpen:', isOpen); // إضافة سجل للتتبع
  const { toast } = useToast();
  const [creditCards, setCreditCards] = useState<CreditCard[]>([]);
  const [isAddingCard, setIsAddingCard] = useState(false);
  const [editingCard, setEditingCard] = useState<CreditCard | null>(null);
  const [newCard, setNewCard] = useState({
    name: '',
    bank: '',
    creditLimit: '',
    startDate: '',
    endDate: '',
    reminderDate: '',
    color: '#8B5CF6',
    holderName: '',
    withdrawLimit: '',
    depositLimit: ''
  });
  const [actionCardId, setActionCardId] = useState<string | null>(null);
  const [actionMode, setActionMode] = useState<'withdraw' | 'deposit' | null>(null);
  const [actionAmount, setActionAmount] = useState('');
  const [actionFee, setActionFee] = useState('');

  const cardColors = [
    '#8B5CF6', '#EF4444', '#10B981', '#F59E0B', 
    '#3B82F6', '#EC4899', '#6366F1', '#14B8A6'
  ];

  // Load credit cards from localStorage
  useEffect(() => {
    const savedCards = localStorage.getItem('creditCards');
    if (savedCards) {
      try {
        const parsed = JSON.parse(savedCards) as any[];
        const normalized = parsed.map((card) => {
          const creditLimit = Number(card.creditLimit) || 0;
          const usedAmount = Number(card.usedAmount) || 0;
          const availableAmount = Number.isFinite(card.availableAmount)
            ? Number(card.availableAmount)
            : Math.max(0, creditLimit - usedAmount);
          const withdrawLimit = Number(card.withdrawLimit) || creditLimit;
          const depositLimit = Number(card.depositLimit) || creditLimit;
          const withdrawUsed =
            Number.isFinite(card.withdrawUsed) && card.withdrawUsed >= 0
              ? Number(card.withdrawUsed)
              : usedAmount;
          const depositUsed =
            Number.isFinite(card.depositUsed) && card.depositUsed >= 0
              ? Number(card.depositUsed)
              : 0;

          return {
            id: String(card.id),
            name: String(card.name || ''),
            bank: String(card.bank || ''),
            creditLimit,
            usedAmount,
            availableAmount,
            startDate: card.startDate || '',
            endDate: card.endDate || '',
            reminderDate: card.reminderDate || '',
            color: card.color || '#8B5CF6',
            holderName: card.holderName || '',
            withdrawLimit,
            depositLimit,
            withdrawUsed,
            depositUsed
          } as CreditCard;
        });
        setCreditCards(normalized);
      } catch {
        setCreditCards([]);
      }
    }
  }, []);

  // Save credit cards to localStorage
  const saveCreditCards = (cards: CreditCard[]) => {
    localStorage.setItem('creditCards', JSON.stringify(cards));
    setCreditCards(cards);
  };

  const addCreditCard = () => {
    if (!newCard.name || !newCard.bank || !newCard.creditLimit) {
      toast({
        title: "خطأ",
        description: "يرجى ملء جميع الحقول المطلوبة",
        variant: "destructive",
      });
      return;
    }

    const creditLimit = parseFloat(newCard.creditLimit);
    const withdrawLimit = newCard.withdrawLimit ? parseFloat(newCard.withdrawLimit) : creditLimit;
    const depositLimit = newCard.depositLimit ? parseFloat(newCard.depositLimit) : creditLimit;
    const card: CreditCard = {
      id: Date.now().toString(),
      name: newCard.name,
      bank: newCard.bank,
      creditLimit,
      usedAmount: 0,
      availableAmount: creditLimit,
      startDate: newCard.startDate,
      endDate: newCard.endDate,
      reminderDate: newCard.reminderDate,
      color: newCard.color,
      holderName: newCard.holderName,
      withdrawLimit,
      depositLimit,
      withdrawUsed: 0,
      depositUsed: 0
    };

    const updatedCards = [...creditCards, card];
    saveCreditCards(updatedCards);
    
    setNewCard({
      name: '',
      bank: '',
      creditLimit: '',
      startDate: '',
      endDate: '',
      reminderDate: '',
      color: '#8B5CF6',
      holderName: '',
      withdrawLimit: '',
      depositLimit: ''
    });
    setIsAddingCard(false);

    toast({
      title: "تم بنجاح",
      description: "تم إضافة بطاقة الائتمان بنجاح",
    });
  };

  const deleteCreditCard = (id: string) => {
    const updatedCards = creditCards.filter(card => card.id !== id);
    saveCreditCards(updatedCards);
    
    toast({
      title: "تم الحذف",
      description: "تم حذف بطاقة الائتمان بنجاح",
    });
  };

  const startEdit = (card: CreditCard) => {
    setEditingCard(card);
    setNewCard({
      name: card.name,
      bank: card.bank,
      creditLimit: card.creditLimit.toString(),
      startDate: card.startDate,
      endDate: card.endDate,
      reminderDate: card.reminderDate,
      color: card.color,
      holderName: card.holderName || '',
      withdrawLimit: (card.withdrawLimit ?? card.creditLimit).toString(),
      depositLimit: (card.depositLimit ?? card.creditLimit).toString()
    });
    setIsAddingCard(true);
  };

  const saveEdit = () => {
    if (!editingCard) return;

    const creditLimit = parseFloat(newCard.creditLimit);
    const withdrawLimit = newCard.withdrawLimit ? parseFloat(newCard.withdrawLimit) : creditLimit;
    const depositLimit = newCard.depositLimit ? parseFloat(newCard.depositLimit) : creditLimit;
    const updatedCard: CreditCard = {
      ...editingCard,
      name: newCard.name,
      bank: newCard.bank,
      creditLimit,
      availableAmount: creditLimit - editingCard.usedAmount,
      startDate: newCard.startDate,
      endDate: newCard.endDate,
      reminderDate: newCard.reminderDate,
      color: newCard.color,
      holderName: newCard.holderName,
      withdrawLimit,
      depositLimit
    };

    const updatedCards = creditCards.map(card => 
      card.id === editingCard.id ? updatedCard : card
    );
    saveCreditCards(updatedCards);
    
    setEditingCard(null);
    setIsAddingCard(false);
    setNewCard({
      name: '',
      bank: '',
      creditLimit: '',
      startDate: '',
      endDate: '',
      reminderDate: '',
      color: '#8B5CF6',
      holderName: '',
      withdrawLimit: '',
      depositLimit: ''
    });

    toast({
      title: "تم التحديث",
      description: "تم تحديث بطاقة الائتمان بنجاح",
    });
  };

  const cancelEdit = () => {
    setEditingCard(null);
    setIsAddingCard(false);
    setNewCard({
      name: '',
      bank: '',
      creditLimit: '',
      startDate: '',
      endDate: '',
      reminderDate: '',
      color: '#8B5CF6',
      holderName: '',
      withdrawLimit: '',
      depositLimit: ''
    });
  };

  const makeWithdrawal = (cardId: string, amount: number) => {
    const updatedCards = creditCards.map(card => {
      if (card.id === cardId) {
        const withdrawLimit = card.withdrawLimit ?? card.creditLimit;
        const currentWithdrawUsed = card.withdrawUsed ?? card.usedAmount ?? 0;
        const newWithdrawUsed = currentWithdrawUsed + amount;
        if (newWithdrawUsed > withdrawLimit) {
          toast({
            title: "خطأ",
            description: "مبلغ السحب يتجاوز حد السحب المحدد للبطاقة",
            variant: "destructive",
          });
          return card;
        }
        const newUsedAmount = card.usedAmount + amount;
        return {
          ...card,
          usedAmount: newUsedAmount,
          availableAmount: card.creditLimit - newUsedAmount,
          withdrawUsed: newWithdrawUsed
        };
      }
      return card;
    });
    saveCreditCards(updatedCards);
  };

  const makeDeposit = (cardId: string, amount: number) => {
    const updatedCards = creditCards.map(card => {
      if (card.id === cardId) {
        const depositLimit = card.depositLimit ?? card.creditLimit;
        const currentDepositUsed = card.depositUsed ?? 0;
        const newDepositUsed = currentDepositUsed + amount;
        if (newDepositUsed > depositLimit) {
          toast({
            title: "خطأ",
            description: "مبلغ الإيداع يتجاوز حد الإيداع المحدد للبطاقة",
            variant: "destructive",
          });
          return card;
        }
        const newUsedAmount = Math.max(0, card.usedAmount - amount);
        return {
          ...card,
          usedAmount: newUsedAmount,
          availableAmount: card.creditLimit - newUsedAmount,
          depositUsed: newDepositUsed
        };
      }
      return card;
    });
    saveCreditCards(updatedCards);
  };

  const getUsagePercentage = (card: CreditCard) => {
    return (card.usedAmount / card.creditLimit) * 100;
  };

  const getWithdrawUsagePercentage = (card: CreditCard) => {
    const limit = card.withdrawLimit ?? card.creditLimit;
    if (!limit || limit <= 0) return 0;
    const used = card.withdrawUsed ?? card.usedAmount ?? 0;
    return Math.min(100, (used / limit) * 100);
  };

  const getDepositUsagePercentage = (card: CreditCard) => {
    const limit = card.depositLimit ?? card.creditLimit;
    if (!limit || limit <= 0) return 0;
    const used = card.depositUsed ?? 0;
    return Math.min(100, (used / limit) * 100);
  };

  const isNearDueDate = (endDate: string) => {
    if (!endDate) return false;
    const today = new Date();
    const dueDate = new Date(endDate);
    const diffTime = dueDate.getTime() - today.getTime();
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays <= 7 && diffDays >= 0;
  };

  const openActionDialog = (cardId: string, mode: 'withdraw' | 'deposit') => {
    setActionCardId(cardId);
    setActionMode(mode);
    setActionAmount('');
    setActionFee('');
  };

  const closeActionDialog = () => {
    setActionCardId(null);
    setActionMode(null);
    setActionAmount('');
    setActionFee('');
  };

  const confirmAction = () => {
    if (!actionMode || !actionCardId) return;
    const amountNumber = parseFloat(actionAmount.replace(',', '.'));
    if (!amountNumber || isNaN(amountNumber) || amountNumber <= 0) {
      toast({
        title: "خطأ",
        description: "يرجى إدخال مبلغ صحيح أكبر من صفر",
        variant: "destructive",
      });
      return;
    }

    if (actionMode === 'withdraw') {
      makeWithdrawal(actionCardId, amountNumber);
    } else {
      makeDeposit(actionCardId, amountNumber);
    }

    closeActionDialog();

    toast({
      title: "تم بنجاح",
      description: actionMode === 'withdraw' ? 'تم تسجيل عملية السحب' : 'تم تسجيل عملية الإيداع',
    });
  };

  const selectedCard = creditCards.find(c => c.id === actionCardId) || null;

  const parsedAmount = parseFloat(actionAmount.replace(',', '.'));
  const parsedFee = parseFloat(actionFee.replace(',', '.'));
  const isParsedAmountValid = !isNaN(parsedAmount) && parsedAmount > 0;
  const isParsedFeeValid = !isNaN(parsedFee) && parsedFee >= 0;
  const netAmount = isParsedAmountValid
    ? Math.max(0, parsedAmount - (isParsedFeeValid ? parsedFee : 0))
    : 0;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[95vh] overflow-y-auto p-3 sm:p-6">
        <DialogHeader className="pb-4">
          <DialogTitle className="flex items-center gap-2 text-lg sm:text-xl">
            <CreditCard className="h-5 w-5 sm:h-6 sm:w-6 text-purple-600" />
            إدارة كروت الائتمان
          </DialogTitle>
        </DialogHeader>

        <div className="space-y-4 sm:space-y-6">
          {/* Add New Card Button */}
          {!isAddingCard && (
            <Button
              onClick={() => setIsAddingCard(true)}
              className="w-full bg-purple-600 hover:bg-purple-700 text-white py-3 text-base"
            >
              <Plus className="h-4 w-4 mr-2" />
              إضافة بطاقة ائتمان جديدة
            </Button>
          )}

          {/* Add/Edit Card Form */}
          {isAddingCard && (
            <div className="bg-gray-50 p-3 sm:p-4 rounded-lg border">
              <h3 className="text-base sm:text-lg font-semibold mb-3 sm:mb-4">
                {editingCard ? 'تعديل بطاقة الائتمان' : 'إضافة بطاقة ائتمان جديدة'}
              </h3>
              
              <div className="grid grid-cols-1 gap-3 sm:gap-4">
                {/* اسم البطاقة */}
                <div>
                  <Label htmlFor="cardName" className="text-sm font-medium">اسم البطاقة</Label>
                  <Input
                    id="cardName"
                    value={newCard.name}
                    onChange={(e) => setNewCard({...newCard, name: e.target.value})}
                    placeholder="مثال: بطاقة التسوق"
                    className="mt-1 h-11"
                  />
                </div>
                
                {/* اسم البنك */}
                <div>
                  <Label htmlFor="bankName" className="text-sm font-medium">اسم البنك</Label>
                  <Input
                    id="bankName"
                    value={newCard.bank}
                    onChange={(e) => setNewCard({...newCard, bank: e.target.value})}
                    placeholder="مثال: البنك الأهلي"
                    className="mt-1 h-11"
                  />
                </div>
                
                {/* الحد الائتماني */}
                <div>
                  <Label htmlFor="creditLimit" className="text-sm font-medium">الحد الائتماني (جنيه)</Label>
                  <Input
                    id="creditLimit"
                    type="number"
                    value={newCard.creditLimit}
                    onChange={(e) => setNewCard({...newCard, creditLimit: e.target.value})}
                    placeholder="10000"
                    className="mt-1 h-11"
                  />
                </div>
                
                {/* لون البطاقة */}
                <div>
                  <Label htmlFor="cardColor" className="text-sm font-medium">لون البطاقة</Label>
                  <div className="flex gap-3 mt-2 flex-wrap">
                    {cardColors.map(color => (
                      <button
                        key={color}
                        type="button"
                        className={`w-10 h-10 rounded-full border-2 ${
                          newCard.color === color ? 'border-gray-800' : 'border-gray-300'
                        }`}
                        style={{ backgroundColor: color }}
                        onClick={() => setNewCard({...newCard, color})}
                      />
                    ))}
                  </div>
                </div>
                
                {/* تاريخ بداية المدة */}
                <div>
                  <Label htmlFor="startDate" className="text-sm font-medium">تاريخ بداية المدة</Label>
                  <Input
                    id="startDate"
                    type="date"
                    value={newCard.startDate}
                    onChange={(e) => setNewCard({...newCard, startDate: e.target.value})}
                    className="mt-1 h-11"
                  />
                </div>
                
                {/* تاريخ نهاية المدة */}
                <div>
                  <Label htmlFor="endDate" className="text-sm font-medium">تاريخ نهاية المدة</Label>
                  <Input
                    id="endDate"
                    type="date"
                    value={newCard.endDate}
                    onChange={(e) => setNewCard({...newCard, endDate: e.target.value})}
                    className="mt-1 h-11"
                  />
                </div>
                
                {/* تاريخ التذكير */}
                <div>
                  <Label htmlFor="reminderDate" className="text-sm font-medium">تاريخ التذكير</Label>
                  <Input
                    id="reminderDate"
                    type="date"
                    value={newCard.reminderDate}
                    onChange={(e) => setNewCard({...newCard, reminderDate: e.target.value})}
                    className="mt-1 h-11"
                  />
                </div>
              </div>
              
              {/* أزرار الحفظ والإلغاء */}
              <div className="flex flex-col sm:flex-row gap-3 mt-4">
                <Button
                  onClick={editingCard ? saveEdit : addCreditCard}
                  className="bg-green-600 hover:bg-green-700 text-white py-3 text-base flex-1"
                >
                  <CheckCircle className="h-4 w-4 mr-2" />
                  {editingCard ? 'حفظ التعديلات' : 'إضافة البطاقة'}
                </Button>
                <Button
                  onClick={cancelEdit}
                  variant="outline"
                  className="py-3 text-base flex-1"
                >
                  إلغاء
                </Button>
              </div>
            </div>
          )}

          {/* Credit Cards List */}
          <div className="space-y-3 sm:space-y-4">
            {creditCards.length === 0 ? (
              <div className="text-center py-6 sm:py-8 text-gray-500">
                <CreditCard className="h-10 w-10 sm:h-12 sm:w-12 mx-auto mb-3 sm:mb-4 text-gray-300" />
                <p className="text-sm sm:text-base">لا توجد بطاقات ائتمان مضافة بعد</p>
              </div>
            ) : (
              creditCards.map((card) => (
                <div
                  key={card.id}
                  className="relative overflow-hidden rounded-2xl p-4 sm:p-5 shadow-md border text-white bg-gradient-to-br from-slate-900 via-slate-900 to-slate-800"
                  style={{ borderColor: card.color }}
                >
                  <div
                    className="pointer-events-none absolute inset-0 opacity-60"
                    style={{
                      background: `radial-gradient(circle at top left, ${card.color}, transparent 55%)`
                    }}
                  />
                  <div className="relative space-y-4">
                    <div className="flex justify-between items-start">
                      <div className="flex-1 text-right">
                        <h4 className="text-base sm:text-lg font-semibold tracking-wide">{card.name}</h4>
                        <p className="text-xs sm:text-sm text-gray-200">{card.bank}</p>
                        {card.holderName && (
                          <p className="mt-1 text-xs sm:text-sm text-gray-100">
                            صاحب البطاقة: <span className="font-medium">{card.holderName}</span>
                          </p>
                        )}
                      </div>
                      <div className="flex flex-col items-end gap-2">
                        <div className="flex items-center gap-2">
                          <CreditCard className="h-5 w-5 text-yellow-300" />
                          <span className="text-lg sm:text-xl font-black tracking-widest">VISA</span>
                        </div>
                        <div className="flex gap-2">
                          <button
                            onClick={() => startEdit(card)}
                            className="p-2 rounded-full bg-white/10 hover:bg-white/20 text-blue-100 transition-colors"
                          >
                            <Edit3 className="h-4 w-4" />
                          </button>
                          <button
                            onClick={() => deleteCreditCard(card.id)}
                            className="p-2 rounded-full bg-white/10 hover:bg-white/20 text-red-100 transition-colors"
                          >
                            <Trash2 className="h-4 w-4" />
                          </button>
                        </div>
                      </div>
                    </div>

                    <div className="space-y-2">
                      <div className="flex justify-between text-xs sm:text-sm text-gray-100">
                        <span>الإجمالي المستخدم</span>
                        <span>
                          {Number(card.usedAmount).toLocaleString('en-US', { maximumFractionDigits: 0 })} / {Number(card.creditLimit).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م
                        </span>
                      </div>
                      <div className="w-full h-1.5 rounded-full bg-white/15 overflow-hidden">
                        <div
                          className="h-full bg-emerald-400"
                          style={{ width: `${Math.min(100, getUsagePercentage(card))}%` }}
                        />
                      </div>
                      <div className="flex justify-between text-[11px] text-gray-200">
                        <span>المتاح: {Number(card.availableAmount).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م</span>
                      </div>
                    </div>

                    <div className="mt-2 space-y-2">
                      <div className="flex justify-between items-center text-xs sm:text-sm text-gray-100">
                        <span>حد السحب</span>
                        <span>
                          {Number((card.withdrawUsed ?? card.usedAmount) || 0).toLocaleString('en-US', { maximumFractionDigits: 0 })} /{' '}
                          {Number(card.withdrawLimit ?? card.creditLimit).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م
                        </span>
                      </div>
                      <div className="h-1.5 rounded-full bg-red-950/60 overflow-hidden">
                        <div
                          className="h-full bg-red-400"
                          style={{ width: `${getWithdrawUsagePercentage(card)}%` }}
                        />
                      </div>
                      <div className="flex justify-between items-center text-xs sm:text-sm text-gray-100 mt-2">
                        <span>حد الإيداع</span>
                        <span>
                          {Number(card.depositUsed ?? 0).toLocaleString('en-US', { maximumFractionDigits: 0 })} /{' '}
                          {Number(card.depositLimit ?? card.creditLimit).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م
                        </span>
                      </div>
                      <div className="h-1.5 rounded-full bg-emerald-950/60 overflow-hidden">
                        <div
                          className="h-full bg-emerald-400"
                          style={{ width: `${getDepositUsagePercentage(card)}%` }}
                        />
                      </div>
                    </div>

                    {isNearDueDate(card.endDate) && (
                      <div className="flex items-center gap-2 p-2 rounded-xl bg-amber-500/15 border border-amber-400/40 text-amber-100 text-xs sm:text-sm">
                        <AlertCircle className="h-4 w-4 flex-shrink-0" />
                        <span>موعد سداد البطاقة قريب، تأكد من السداد في الوقت المحدد.</span>
                      </div>
                    )}

                    {(card.startDate || card.endDate || card.reminderDate) && (
                      <div className="grid grid-cols-1 gap-1.5 text-[11px] sm:text-xs text-gray-100">
                        {card.startDate && (
                          <div className="flex items-center gap-2">
                            <Calendar className="h-3 w-3 flex-shrink-0" />
                            <span>البداية: {new Date(card.startDate).toLocaleDateString('ar-EG')}</span>
                          </div>
                        )}
                        {card.endDate && (
                          <div className="flex items-center gap-2">
                            <Calendar className="h-3 w-3 flex-shrink-0" />
                            <span>النهاية: {new Date(card.endDate).toLocaleDateString('ar-EG')}</span>
                          </div>
                        )}
                        {card.reminderDate && (
                          <div className="flex items-center gap-2">
                            <AlertCircle className="h-3 w-3 flex-shrink-0" />
                            <span>التذكير: {new Date(card.reminderDate).toLocaleDateString('ar-EG')}</span>
                          </div>
                        )}
                      </div>
                    )}

                    <div className="grid grid-cols-2 gap-2 pt-2">
                      <Button
                        onClick={() => openActionDialog(card.id, 'withdraw')}
                        className="w-full bg-red-500 hover:bg-red-600 text-white py-2.5 text-sm"
                        disabled={card.availableAmount <= 0}
                      >
                        <ArrowDown className="h-4 w-4 mr-2" />
                        سحب
                      </Button>
                      <Button
                        onClick={() => openActionDialog(card.id, 'deposit')}
                        className="w-full bg-emerald-500 hover:bg-emerald-600 text-white py-2.5 text-sm"
                      >
                        <ArrowUp className="h-4 w-4 mr-2" />
                        إيداع
                      </Button>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>

        <Dialog open={!!actionMode} onOpenChange={(open) => { if (!open) closeActionDialog(); }}>
          <DialogContent className="max-w-md">
            <DialogHeader>
              <DialogTitle className="text-lg font-semibold">
                {actionMode === 'withdraw' ? 'سحب من البطاقة' : 'إيداع في البطاقة'}
              </DialogTitle>
            </DialogHeader>
            <div className="space-y-4">
              {selectedCard && (
                <div className="rounded-lg bg-gray-50 p-3 text-sm text-gray-700">
                  <div className="flex justify-between items-center mb-1">
                    <span className="font-medium">{selectedCard.name}</span>
                    <span className="text-xs text-gray-500">{selectedCard.bank}</span>
                  </div>
                  {selectedCard.holderName && (
                    <div className="text-xs text-gray-600">
                      صاحب البطاقة: <span className="font-medium">{selectedCard.holderName}</span>
                    </div>
                  )}
                  <div className="mt-2 grid grid-cols-2 gap-2 text-xs">
                    <div className="rounded-md bg-white border px-2 py-1">
                      <div className="text-gray-500">المتاح</div>
                      <div className="font-semibold text-gray-800">
                        {Number(selectedCard.availableAmount).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م
                      </div>
                    </div>
                    <div className="rounded-md bg-white border px-2 py-1">
                      <div className="text-gray-500">المستخدم</div>
                      <div className="font-semibold text-gray-800">
                        {Number(selectedCard.usedAmount).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م
                      </div>
                    </div>
                  </div>
                </div>
              )}

              <div className="space-y-3">
                <div>
                  <Label htmlFor="actionAmount" className="text-sm font-medium">
                    {actionMode === 'withdraw' ? 'مبلغ السحب' : 'مبلغ الإيداع'}
                  </Label>
                  <Input
                    id="actionAmount"
                    type="number"
                    value={actionAmount}
                    onChange={(e) => setActionAmount(e.target.value)}
                    className="mt-1 h-10"
                    placeholder="0.00"
                  />
                </div>
                <div>
                  <Label htmlFor="actionFee" className="text-sm font-medium">
                    العمولة المخصومة من المبلغ
                  </Label>
                  <Input
                    id="actionFee"
                    type="number"
                    value={actionFee}
                    onChange={(e) => setActionFee(e.target.value)}
                    className="mt-1 h-10"
                    placeholder="0.00"
                  />
                  {isParsedAmountValid && (
                    <p className="mt-1 text-xs text-gray-600">
                      الصافي بعد العمولة:{' '}
                      <span className="font-semibold text-gray-800">
                        {Number(netAmount).toLocaleString('en-US', { maximumFractionDigits: 0 })} ج.م
                      </span>
                    </p>
                  )}
                </div>
              </div>

              <div className="flex flex-col sm:flex-row gap-3 pt-2">
                <Button
                  onClick={confirmAction}
                  className={`flex-1 h-10 ${
                    actionMode === 'withdraw'
                      ? 'bg-red-600 hover:bg-red-700'
                      : 'bg-emerald-600 hover:bg-emerald-700'
                  } text-white`}
                >
                  <DollarSign className="h-4 w-4 mr-2" />
                  تأكيد
                </Button>
                <Button
                  onClick={closeActionDialog}
                  variant="outline"
                  className="flex-1 h-10"
                >
                  إلغاء
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </DialogContent>
    </Dialog>
  );
};

export default CreditCardsDialog;
